import React, { Component } from "react";
import Card from "./Card";

export default class LifeCycle extends Component {
  state = {
    number: 1,
  };
  componentDidMount() {
    // thng dng  call api hin th danh sch khi user vo trang
    console.log("componentDidMount");
    // chy 1 ln / sau khi render() chy
  }
  shouldComponentUpdate(newProps, newState) {
    console.log("state mi", newState);
    console.log("state c", this.state);
    // mc nh return true / khi return false th khng chy lnh render

    // khi number ==5 th khng cho render
    if (newState.number === 5) {
      return false;
    }
    return true;
  }
  render() {
    console.log("render");
    //  chy nhiu ln / khi setState
    return (
      <div>
        <button
          className="btn btn-dark"
          onClick={() => {
            this.setState({ number: this.state.number - 1 });
          }}
        >
          -
        </button>
        <strong>{this.state.number}</strong>
        <button
          className="btn btn-dark"
          onClick={() => {
            this.setState({ number: this.state.number + 1 });
          }}
        >
          +
        </button>
        {this.state.number < 3 && <Card />}
      </div>
    );
  }

  componentWillUnmount() {
    console.log("componentWillUnmount");
    // chy 1 ln / trc khi component b xo khi giao din
  }
}
