import { TAI, XIU, PLAY } from "./constantDice";
const initialState = {
  dice: [
    {
      diceMini: "./img/imgDice/1.png",
      ma: 1,
    },
    {
      diceMini: "./img/imgDice/2.png",
      ma: 2,
    },
    {
      diceMini: "./img/imgDice/3.png",
      ma: 3,
    },
    // {
    //   diceMini: "./img/imgDice/4.png",
    //   ma: 4,
    // },
    // {
    //   diceMini: "./img/imgDice/5.png",
    //   ma: 5,
    // },
    // {
    //   diceMini: "./img/imgDice/6.png",
    //   ma: 6,
    // },
  ],

  chosen: "",
  win: 0,
  total: 0,
};

export let diceReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case TAI: {
      let taiNe = "TAI";
      console.log(payload);
      let cloneChosen = [...state.chosen];
      cloneChosen = taiNe;

      return { ...state, chosen: cloneChosen };
    }
    case XIU: {
      let xiuNe = "XIU";
      console.log(payload);
      let cloneChosen = [...state.chosen];
      cloneChosen = xiuNe;
      return { ...state, chosen: cloneChosen };
    }
    case PLAY: {
      let cloneDice = [...state.dice];
      state.total++;
      for (let i = 0; i < 3; i++) {
        let random = Math.floor(Math.random() * 6) + 1;
        console.log(" ~ diceReducer ~ random:", random);
        if (random == 6) {
          cloneDice[i].diceMini = "./img/imgDice/6.png";
          cloneDice[i].ma = 6;
          console.log(cloneDice);
          console.log(i);
        } else if (random == 5) {
          cloneDice[i].diceMini = "./img/imgDice/5.png";
          cloneDice[i].ma = 5;

          console.log(cloneDice);
          console.log("index", i);
        } else if (random == 4) {
          cloneDice[i].diceMini = "./img/imgDice/4.png";
          cloneDice[i].ma = 4;

          console.log(cloneDice);
          console.log("index", i);
        } else if (random == 3) {
          cloneDice[i].diceMini = "./img/imgDice/3.png";
          cloneDice[i].ma = 3;

          console.log(cloneDice);
          console.log("index", i);
        } else if (random == 2) {
          cloneDice[i].diceMini = "./img/imgDice/2.png";
          cloneDice[i].ma = 2;
          console.log(cloneDice);
          console.log("index", i);
        } else {
          cloneDice[i].diceMini = "./img/imgDice/1.png";
          cloneDice[i].ma = 1;
          console.log(cloneDice);
          console.log("index", i);
        }
      }
      let totalWin = cloneDice
        .map((item) => item.ma)
        .reduce((accumulator, currentValue) => accumulator + currentValue, 0);
      console.log(totalWin);

      if (state.chosen === "TAI") {
        if (totalWin > 10) {
          state.win++;
        }
      } else if (state.chosen === "XIU") {
        if (totalWin > 10) {
          state.win++;
        }
      }

      return { ...state, dice: cloneDice };
    }

    default:
      return state;
  }
};
